import React from 'react';
import { connect } from 'react-redux';
import { fetchItems, deleteItem } from '../actions';

class List extends React.Component {
  componentDidMount(){
    this.props.fetchItems();
  }

  renderList() {
    console.log(this.props.items);
    return this.props.items.map(item => {
      return (
        <div className="column is-one-third">
          <div className="message" key={item.id}>
            <div className="message-header">
              <p>{item.description}</p>
              <button onClick={() => this.props.deleteItem(item.id) } className="delete"></button>
            </div>
            <div className="message-body">
              Expiry Date: {item.expiryDateMonth}/{item.expiryDateYear}
              <br></br>
              Category: {item.category}
            </div>
          </div>
        </div>
      );
    });
  }

  render() {
    return (
      <div className="columns is-multiline is-mobile">
        {this.renderList()}
      </div>
    );
  }
};

const mapStateToProps = (state) => {
  return {items: Object.values(state.items)}
}

export default connect(mapStateToProps, { fetchItems, deleteItem })(List);