import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { createItem } from '../actions';

class Input extends React.Component {

  renderError({ error, touched }) {
    if(touched && error) {
      return (
        <div className="help is-danger">{error}</div>
      );
    }
  };

  renderInput = ({ input, label, meta }) => {
    const classCheck = `input ${meta.error && meta.touched ? 'is-danger': ''}`;
    return (

      <div className="field is-horizontal column is-four-fifths">
        <div className="field-label is-normal">
          <label className="label">{label}</label>
        </div>

        <div className="field-body">
          <div className="field">
            <div className="control">
              <input
                className={classCheck}
                type="text"
                placeholder="Text input"
                {...input} autoComplete="off"
              ></input>
              {this.renderError(meta)}
            </div>

          </div>

        </div>

      </div>


    );
  }

  renderExpiryMonth = ({input, label, meta}) => {
    const classCheck = `select ${meta.error && meta.touched ? 'is-danger': ''}`;
    return (
      <div className="field is-horizontal column is-four-fifths">
        <div className="field-label is-normal">
          <label className="label">{label}</label>
        </div>

        <div className="field-body">
          <div className="control">
              <span className={classCheck}>
                <select {...input}>
                  <option>Select</option>
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                  <option>6</option>
                  <option>7</option>
                  <option>8</option>
                  <option>9</option>
                  <option>10</option>
                  <option>11</option>
                  <option>12</option>
                </select>
              </span>
              {this.renderError(meta)}
          </div>
        </div>
      </div>
    );
  }

  renderExpiryYear = ({ input, label, meta }) => {
    const classCheck = `select ${meta.error && meta.touched ? 'is-danger': ''}`;
    return(
      <div className="field is-horizontal column is-four-fifths">
        <div className="field-label is-normal">
          <label className="label">{label}</label>
        </div>
        <div className="field-body">
          <div className="control">
            <span className={classCheck}>
              <select {...input}>
                <option>Select</option>
                <option>2020</option>
                <option>2021</option>
                <option>2022</option>
                <option>2023</option>
                <option>2024</option>
                <option>2025</option>
              </select>
            </span>
            {this.renderError(meta)}
          </div>
        </div>
      </div>
    );
  }

  renderCategory = ({ input, label, meta }) => {
    const classCheck = `select ${meta.error && meta.touched ? 'is-danger': ''}`;
    return (
      <div className="field is-horizontal column is-four-fifths">
        <div className="field-label is-normal">
          <label className="label">{label}</label>
        </div>

        <div className="field-body">
          <div className="control">
              <span className={classCheck}>
                <select {...input}>
                  <option>Select</option>
                  <option>Fridge</option>
                  <option>Freezer</option>
                </select>
              </span>
              {this.renderError(meta)}
          </div>


        </div>
      </div>
    );
  }

  renderButton(){
    return(
      <div className="field is-horizontal column is-four-fifths">
        <div className="field-label">
          {/* Left empty for spacing */}
        </div>

        <div className="field-body">
          <div className="field">
            <div className="control">
              <button className="button is-primary">Submit</button>
            </div>
          </div>
        </div>

      </div>
    );
  }

  onSubmit = (formValues) => {
    this.props.createItem(formValues);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit(this.onSubmit)}>
        <Field name="description" component={this.renderInput} label="Item Name" />
        <Field name="expiryDateMonth" component={this.renderExpiryMonth} label="Expiry Date Month" />
        <Field name="expiryDateYear" component={this.renderExpiryYear} label="Expiry Date Year" />
        <Field name="category" component={this.renderCategory} label="Category" />
        <Field name="button" component={this.renderButton} />
      </form>
    );
  }

};

const validate = formValues => {
  const errors = {};
  if(!formValues.description) {
    errors.description = 'You must enter the item description'
  }
  if(!formValues.expiryDateMonth || formValues.expiryDateMonth === "Select") {
    errors.expiryDateMonth = "You must select month of expiry"
  }
  if(!formValues.expiryDateYear || formValues.expiryDateYear === "Select") {
    errors.expiryDateYear = "You must select year of expiry"
  }
  if(!formValues.category || formValues.category === "Select") {
    errors.category = "You must select a category"
  }

  return errors;
};

const formWrapped = reduxForm({
  form: 'itemCreate',
  validate
})(Input);

export default connect(null, {createItem})(formWrapped);